export default {
    name: "cash-log",
    components: {},
    data() {
        return {
            page: 1,
            args: false,
            load: false,
            mch_id: 0,
            list: [],

            date_a: '',
            data: '',
        }
    },
    onLoad(options) { this.$commonLoad.onload(options);
        this.mch_id = options.mch_id;
        this.getNowTime(new Date());
    },

    onReachBottom: function () {
        const self = this;
        if (self.args || self.load)
            return;
        self.load = true;
        let page = self.page + 1;

        self.$request({
            url: self.$api.mch.cash_log,
            data: {
                mch_id: self.mch_id,
                page: page,
            }
        }).then(info => {
            if (info.code === 0) {
                [self.page, self.args, self.list] = [page, info.data.list.length === 0, self.list.concat(info.data.list)];
            }
            self.load = false;
        });
    },

    methods: {
        getLog: function () {
            const self = this;
            self.$showLoading();

            self.$request({
                url: self.$api.mch.cash_log,
                data: {
                    mch_id: self.mch_id,
                    date: self.date
                }
            }).then(info => {
                self.$hideLoading();
                self.list = info.data.list;
            }).catch(e => {
                self.$hideLoading();
            })
        },

        dateLess: function () {
            let date = this.date;
            let d = new Date(date);
            d.setMonth(d.getMonth() - 1);
            this.getNowTime(d);
        },
        datePlus: function () {
            let date = this.date;
            let d = new Date(date);
            d.setMonth(d.getMonth() + 1);
            this.getNowTime(d);
        },
        dateChange: function (e) {
            let date = e.detail.value;
            let d = new Date(date);
            this.getNowTime(d);
        },

        getNowTime(date) {
            let year = date.getFullYear();
            let month = date.getMonth() + 1;
            date = [year, month].map((n) => {
                n = n.toString()
                return n[1] ? n : '0' + n
            }).join('-');
            let date_a = date.replace('-', '年') + '月';
            [this.date, this.date_a, this.page, this.args] = [date, date_a, 1, false];
            this.getLog();
        },
    }
}
